import { check } from 'k6';
import { Options } from 'k6/options';
import http from 'k6/http';

// Configuration options for the load test
export let options: Options = {
  // Stages for ramping up/down VUs
  stages: [
    { duration: '30s', target: 20 }, // Ramp up to 20 VUs over 30s
    { duration: '1m', target: 20 },  // Stay at 20 VUs for 1 minute
    { duration: '30s', target: 0 },  // Ramp down to 0 VUs over 30s
  ],
  thresholds: {
    // Define performance thresholds
    http_req_duration: ['p(95)<1000'], // 95% of requests should be below 1s
    http_req_failed: ['rate<0.05'],    // Error rate should be below 5%
  },
};

// Sample transaction data
const createTransaction = () => {
  return {
    amount: Math.floor(Math.random() * 1000),
    description: `Transaction ${new Date().toISOString()}`,
    type: Math.random() > 0.5 ? 'deposit' : 'withdrawal',
  };
};

// Environment variables (can be overridden with K6_VARS)
const API_BASE_URL = __ENV.API_BASE_URL || 'http://localhost:3000/api';
const WALLET_ID = __ENV.WALLET_ID || 'test-wallet-id';

export default () => {
  // Create transaction payload
  const payload = JSON.stringify(createTransaction());
  
  // Set headers
  const params = {
    headers: {
      'Content-Type': 'application/json',
      'Accept': 'application/json',
    },
  };

  // Send POST request
  const res = http.post(
    `${API_BASE_URL}/wallets/${WALLET_ID}/transactions`,
    payload,
    params
  );

  // Verify the response
  check(res, {
    'status is 201': () => res.status === 201, // Expecting 201 Created
    'transaction created': () => {
      try {
        const body = JSON.parse(res.body as string);
        return body && typeof body === 'object' && body.id !== undefined;
      } catch {
        return false;
      }
    },
  });
}; 